#pc .wrapper { border-radius: 5px; position: absolute; left: 50%; top: 0; transform: translateX(-50%); width: 100px; height: 40px; background: #dec1a1; color: #fff; text-align: center; }

#pc .wrapper .icon-cloud-upload { font-size: 1.5714285714rem; line-height: 2.6428571429rem; }

#pc .wrapper p { color: #777777; font-size: 0.7142857143rem; font-family: "Cairo", sans-serif; font-weight: 700; margin-top: 11px; }

#pc .uppy-FileInput-container { position: absolute; left: 50%; top: 0; display: inline-block; transform: translateX(-50%); }

#pc .uppy-FileInput-btn { opacity: 0; }

.wrapper-file-added { padding-bottom: 20px; margin-bottom: 30px; border-bottom: 1px solid #707070; }

.file-added { display: flex; flex-wrap: wrap; border-bottom: 1px solid #707070; padding-bottom: 28px; margin-bottom: 35px; font-family: "Tajawal", sans-serif; font-weight: 700; color: #b1b1b1; }

.file-added div p { font-size: 0.8571428571rem; color: #707070; margin-bottom: 8px; }

.file-added div span { font-size: 0.7142857143rem; display: inline-block; direction: ltr; }

.file-added.uploaded { padding-bottom: 0; border-bottom: none; margin-bottom: 20px; }

.file-added.uploaded .exe ~ div { display: flex; justify-content: space-between; flex: 1 0 auto; }

.exe { height: 35px; min-width: 40px; background: #f4f4f4; border: 1px solid #d9d9d9; display: flex; justify-content: center; align-items: center; font-size: 0.7142857143rem; font-family: "Tajawal", sans-serif; text-transform: uppercase; margin-left: 8px; margin-bottom: 0; padding: 0 10px; }

.postioning-toast { position: fixed; top: 100px; right: 100px; z-index: 10000; }

.postioning-toast .toast-style { color: #fff; }

.postioning-toast .toast-style .toast-body { padding: 15px 12px; font-size: 1rem; line-height: 1.5714285714rem; margin-bottom: 20px; display: flex; align-items: center; border-radius: 5px; }

.postioning-toast .toast-style .toast-body i { margin-left: 14px; }

.postioning-toast .toast-style .toast-body button { background-color: transparent; border-color: transparent; color: #fff; padding-right: 20px; }

.postioning-toast .toast-style .toast-body button:focus { outline: none; }

.postioning-toast .toast-style.success_toast { color: #00b4a6; background-color: #00b4a633; }

.postioning-toast .toast-style.success_toast .toast-body { border: 1px solid #00b4a6; }

.postioning-toast .toast-style.error_toast { color: #ff595a; background-color: #ff595a33; }

.postioning-toast .toast-style.error_toast .toast-body { border: 1px solid #ff595a; }

.no-noti { color: #000; text-align: center; width: 100%; margin-bottom: 8px; }

.sidebar-policy { display: flex; flex-direction: column; place-items: flex-end; }

.sidebar-policy .logo { background-image: url("../../images/logo-black.svg"); }

.sidebar-policy .contact-ways { margin-top: 30px; }

.sidebar-policy .contact-ways p { direction: ltr; text-align: left; color: #000033; font-size: 0.7142857143rem; line-height: 1.2142857143rem; }

.sidebar-policy .contact-ways p:first-of-type { font-family: "DiodrumArabic-Bold", "sans-serif"; font-size: 0.7857142857rem; margin-bottom: 20px; }

.sidebar-policy .copyright { margin-top: 25px; font-size: 0.7142857143rem; color: #707070; }

.privacy-policy_content li { font-family: "Tajawal", sans-serif; line-height: 1.7142857143rem; color: #181b1f; list-style: none; position: relative; }

.privacy-policy_content li:before { content: ''; background: #0000000d; display: inline-block; width: 12px; height: 12px; margin-right: -14px; border-radius: 50%; position: absolute; right: -14px; top: 5px; }

.container-post__left_side .tab-content.more-info div.block-content_side { padding: 10px 20px; }

.container-post__left_side .tab-content.more-info div.block-content_side > h2 { font-family: "DiodrumArabic-Bold", "sans-serif"; font-size: 1rem; color: #000; margin-top: 15px; margin-bottom: 30px; }

.container-post__left_side .tab-content.more-info div.block-content_side > p { color: #707070; font-size: 0.8571428571rem; line-height: 1.4285714286rem; }

/* end of pages styling */
@media (max-width: 1280px) { .sign-window h1 { margin-bottom: 15px; font-size: 2.1428571429rem; line-height: 2.1428571429rem; margin-top: 35px; }
  .sign-window p { margin-bottom: 0; }
  .sign-window .sign-window-form { margin-top: 20px; } }

@media (max-width: 1024px) and (min-width: 992px) { html { font-size: 12px; }
  body.fixed-sidebar .sidebar { width: 245px; }
  .section-lg.center-img { background-size: 375px; background-position: -20px 260px; }
  .blue-section { margin-top: -6px; }
  .section-md .section-img { width: 448px; }
  .section-md#path-id { margin-top: -2px; }
  .section-md#path-id .hero-wrapper:nth-of-type(2) .section-img { width: 445px; }
  .section-md#path-id .hero-wrapper:nth-of-type(3) .section-img { width: 364px; }
  .section-md.common-section .section-img { width: 356px; }
  .green-section { margin-top: -2px; }
  .line-through-section .card-wrapper:first-of-type { padding: 0 170px; }
  .line-through-section .card-wrapper:nth-of-type(2) { margin: 42px 0px 40px -15px; }
  .line-through-section .card-wrapper:nth-of-type(3) { padding: 0 170px; }
  .workshop_partition { padding: 12px 10px; }
  .workshop_partition span + span { margin-right: 5px; margin-left: 5px; } }

@media (max-width: 991px) and (min-width: 768px) { .section-lg.center-img { background-size: 360px; }
  .section-md .section-img { width: 308px; }
  .section-md .section-img + .section-text, .section-md .section-text + .section-img { margin-right: 30px; }
  .section-md#path-id { margin-top: -1px; }
  .section-md#path-id .hero-wrapper:nth-of-type(2) .section-img { width: 308px; }
  .section-md#path-id .hero-wrapper:nth-of-type(3) .section-img { width: 308px; }
  .section-md#path-id .hero-wrapper:nth-of-type(3) .section-text p:last-of-type { max-width: 299px; }
  .section-md.line-through-section .line-throught-container { background-size: 710px; }
  .section-md.line-through-section .card-wrapper:first-of-type { padding: 0 45px; }
  .section-md.line-through-section .card-wrapper:nth-of-type(2) { margin: 20px -32px 20px -32px; }
  .section-md.line-through-section .card-wrapper:nth-of-type(3) { padding: 0 45px; }
  .section-md.common-section .section-text { flex: 0 0 352px; }
  .section-md.blue-section { margin-top: -2px; }
  .section-md.blue-section .section-img { width: 308px; }
  body.fixed-sidebar main { height: 100%; }
  body.fixed-sidebar .sidebar { width: 200px; top: 165px; }
  .main-header .logo, .main-header .nav { margin-bottom: 20px; }
  main .container-post-wrapper { margin-top: 8px; }
  main .container-post-wrapper .container-post__center { flex: 0 0 80%; max-width: 80%; }
  main .container-post-wrapper .container-post__right_side { flex: 0 0 20%; max-width: 20%; }
  main .container-post-wrapper .container-post__left_side { display: none; }
  #nav_wrap.logged ~ main { padding-top: 136px; }
  .card-testmonilas { width: 100%; }
  .card-testmonilas .front { background-size: cover; }
  .content-section_header.tabs .list-group { flex-wrap: wrap; } }
