header {
    padding: 20px 0;
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    width: 100%;
    z-index: 1020;
    min-height: 80px;

    .header-wrapper {
        display: flex;
        justify-content: space-between;
        align-items: center;
        position: relative;

        form.logged-in {
            flex-grow: 1;
            max-width: 510px;
        }
    }

    p {
        color: #ffffff;
        font-size: rem(12);
        line-height: rem(14);
        display: inline-block;
        margin-bottom: 0;
        vertical-align: middle;
    }

    &:not(.main-header) {
        .btn {
            margin-right: 30px;
        }
    }

    &.main-header {
        background-color: #fff;
        // position: relative;
        display: block;
        box-shadow: $boxShadow;

        .header-wrapper {
            justify-content: center;

        }

        .nav {
            .nav-item {

                .nav-link {
                    &.active {
                        background-color: #0000000D;
                        border-radius: 5px;
                        padding: 7px 14px;
                    }
                }

                &:not(:last-of-type) {
                    .nav-link {
                        margin-left: 17px;
                    }
                }
            }
        }

        .btn {
            &.text-white {
                margin-right: 0;

                &:last-of-type {
                    margin-right: 20px;
                }
            }
        }

    }


    &.header-post {
        padding: 0;
        box-shadow: none;

        .container-post-wrapper {
            align-items: center;
        }

        .container-post__left_side,
        .container-post__center,
        .container-post__right_side {
            padding: 15px 0;
        }

        .container-post__right_side {
            border: 1px solid #D9D9D9;
            height: 80px;
            display: flex;
            align-items: center;
            justify-content: center;
        }

        .container-post__center {
            padding-right: 15px;
        }

        .container-post__left_side {
            .btn {
                display: inline-flex;
                align-items: center;

                i+span {
                    margin-right: 8px;
                }

                &+.btn {
                    margin-right: 15px;
                }

                i {

                    &.icon-download,
                    &.icon-upload {
                        font-size: rem(14);
                    }

                    &.icon-preview {
                        font-size: rem(10);
                    }
                }
            }
        }

    }
}

#nav_wrap {
    &.relative-header {
        header {
            position: relative;
        }

        &~main {
            padding-top: 0;
        }

        &~footer {
            display: block;
        }

    }
}


// content section header
.content-section_header {
    font-family: $DiodrumM;
    width: 100%;
    margin-bottom: 35px;
    display: flex;
    align-items: center;
    justify-content: space-between;

    &.tabs {
        padding-bottom: 28px;
        border-bottom: 1px solid #CCCCCC;

    }

    h2 {
        font-size: rem(16);
        line-height: rem(19);
        margin-bottom: 0;
    }

    .list-group {
        flex-direction: row;

    }
}


@media (max-width: 1024px) and (min-width: 992px) {
    header.header-post {
        .container-post__left_side {
            flex: 0 0 26.945313%;
            max-width: 26.945313%
        }
        .container-post__center {
            flex: 0 0 63.390625%;
            max-width: 63.390625%;
        }
    }
}
@media (max-width: 991px) and (min-width: 768px) {
    header.header-post {
        .container-post__center {
            flex: 0 0 80%;
            max-width: 80%;
        }
        .container-post__right_side {
            flex: 0 0 20%;
            max-width: 20%;
        }
        .container-post__left_side {
            flex: 0 0 100%;
            max-width: 100%;
            text-align: center;
        }
    } 
    .main-header {
        .logo , .nav {
            margin-bottom: 20px;
        }
    }
}

@include media-breakpoint-down(md) {
    .header-wrapper {
        flex-wrap: wrap;
    }
}