/* Global */
html {
	font-size: 14px;
}

/* body styling */
body {
	direction: rtl;
	text-align: right;
	font-family: $DiodrumM;
	font-size: rem(14);
	line-height: rem(17);
	color: #000;

	&.fixed-sidebar {
		overflow: hidden;

		main {
			height: 100vh;
			overflow: scroll;
		}

		.sidebar {
			position: fixed;
			top: 135px;
			left: 0;
			width: 276px;
		}

		& > footer,
		#nav_wrap.main_nav ~ footer {
			display: none;
		}
	}
}

header {
	&.main-header {
		&:not(.relative-header) {
			& ~ main {
				padding-top: 80px;
			}
		}

		& ~ footer {
			display: block;
		}
	}
}

#nav_wrap {
	&.logged {
		& ~ main {
			padding-top: 80px;
		}
	}

	&.main_nav {
		& ~ footer {
			display: block;
		}
	}
}

main {
	min-height: 100vh;
}

img {
	max-width: 100%;
	height: auto;
}

h1 {
	font-family: $DiodrumB;
	font-size: rem(40);
