/* sign window styling */
.sign-window {
	color: #ffffff;

	h1 {
		margin-bottom: 20px;
		text-align: center;
	}

	p {
		font-size: rem(12);
		line-height: rem(14);
		text-align: center;
	}

	.sign-window-form {
		margin-top: 30px;

		.select2-container .select2-selection--single {
			min-height: 40px;
		}

		//container
		.select2-container--default .select2-selection--single {
			background-color: transparent;
			border: none;
			display: flex;
			justify-content: flex-start;
			align-items: center;

			&:focus {
				outline: none;
			}
		}

		//text
		.select2-container--default
			.select2-selection--single
			.select2-selection__rendered {
			color: #ffffff;
			height: 100%;
			display: flex;
			justify-content: flex-start;
			align-items: center;
			padding: 0;

			// margin-right: 20px;
		}

		.select2-container .select2-selection--single .select2-selection__rendered {
			overflow: initial;
			text-overflow: initial;
		}

		//arrow
		.select2-container--default[dir='rtl']
			.select2-selection--single
			.select2-selection__arrow {
			height: 100%;
			top: 0;
			width: 5px;
		}

		.select2-container--default
			.select2-selection--single
			.select2-selection__arrow {
			position: static;
			display: flex;
			align-items: center;
			margin-right: 10px;

			b {
				position: static;
				border-color: rgba(#fff, 0.2) transparent transparent transparent;
				// opacity: 0.2;
			}
		}

		.select2-container--default.select2-container--open
			.select2-selection--single
			.select2-selection__arrow
			b {
			border-color: transparent transparent rgba(#fff, 0.2) transparent;
		}

		.selection:focus {
			outline: none;
		}

		.select2-dropdown {
			background-color: #000033;
			min-width: 100px;
		}

		.select2-container--open .select2-dropdown--below {
			border-top: 1px solid #ffffff;
			z-index: 1100;
		}

		.select2-container--default .select2-results__option[aria-disabled='true'] {
			color: #ffffff;
			opacity: 0.5;
		}

		.select2-container--default
			.select2-results__option--highlighted[aria-selected] {
			background-color: #ffffff;
			color: #000033;
		}

		.select2-results__option {
			color: #ffffff;
			padding: 10px;
		}

		.btn-block {
			margin-top: 30px;
		}

		p {
			margin-top: 15px;
			line-height: rem(24);

			// opacity: 0.5;
			a {
				border-bottom: 1px solid rgba(#fff, 0.5);
				padding-bottom: 5px;
				text-decoration: none;
				display: inline-block;
			}
		}
	}
}

