// mo3lm & moysr word animation
.animation {
	vertical-align: text-bottom;
	height: 115px;
	min-width: 159px;
	display: inline-block;
	overflow: hidden;
	transform-style: preserve-3d;

	ul {
		padding: 0;
		list-style: none;
		transform-style: preserve-3d;
		margin-bottom: 0;

		li {
			color: $primary;
			transform-style: preserve-3d;
			padding-left: 25px;
			padding-right: 25px;
			font-size: rem(44);
			line-height: rem(50);
			text-align: center;

			&:nth-of-type(1) {
				animation: rotateWord1 4s ease 1s infinite;
				filter: blur(4px);
				font-size: rem(34);
			}

			&:nth-of-type(2) {
				animation: rotateWord2 4s ease 1s infinite;
			}

			& + li {
				margin-top: 15px;
			}
		}
	}
}

@keyframes rotateWord1 {
	0% {
		filter: blur(4px);
		font-size: rem(34);
	}

	50% {
		transform: translateY(65px);
		opacity: 1;
		filter: blur(0px);
		font-size: rem(44);
	}

	65% {
		transform: translateY(85px);
		opacity: 0;
		font-size: rem(34);
	}

	66% {
		transform: translateY(-65px);
		opacity: 0;
	}

	80% {
		opacity: 0;
	}

	100% {
		transform: translateY(0);
		filter: blur(4px);
		font-size: rem(34);
		opacity: 1;
	}
}

@keyframes rotateWord2 {
	0% {
		filter: blur(0px);
		font-size: rem(44);
	}

	25% {
		transform: translateY(35px);
	}

	26% {
		transform: translateY(-100px);
		opacity: 0;
		font-size: rem(34);
	}

	50% {
		transform: translateY(-70px);
		filter: blur(4px);
		font-size: rem(34);
	}

	100% {
		transform: translateY(0);
		filter: blur(0px);
		font-size: rem(44);
	}
}

// loading spinner
.loading-wrapper {
	position: fixed;
	height: 100vh;
	width: 100vw;
	z-index: 10000;
	background: rgba(255, 255, 255, 0.9);
	display: flex;
	justify-content: center;
	align-items: center;
	top: 85px;

	.lds-ring {
		display: inline-block;
		position: relative;
		width: 64px;
		height: 64px;
		margin-bottom: 187px;
	}

	.lds-ring div {
		box-sizing: border-box;
		display: block;
		position: absolute;
		width: 51px;
		height: 51px;
		margin: 6px;
		border: 6px solid #ffc055;
		border-radius: 50%;
		animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
		border-color: #ffc055 transparent transparent transparent;
	}

	.lds-ring div:nth-child(1) {
		animation-delay: -0.45s;
	}

	.lds-ring div:nth-child(2) {
		animation-delay: -0.3s;
	}

	.lds-ring div:nth-child(3) {
		animation-delay: -0.15s;
	}

	@keyframes lds-ring {
		0% {
			transform: rotate(0deg);
		}

		100% {
			transform: rotate(360deg);
		}
	}
}

