		transform: translateX(-50%);
	}

	.uppy-FileInput-btn {
		opacity: 0;
	}
}

.wrapper-file-added {
	padding-bottom: 20px;
	margin-bottom: 30px;
	border-bottom: 1px solid #707070;
}

.file-added {
	display: flex;
	flex-wrap: wrap;
	border-bottom: 1px solid #707070;
	padding-bottom: 28px;
	margin-bottom: 35px;
	font-family: $taj;
	font-weight: $bold;
	color: #b1b1b1;

	div {
		p {
			font-size: rem(12);
			color: #707070;
			margin-bottom: 8px;
		}

		span {
			font-size: rem(10);
			display: inline-block;
			direction: ltr;
		}
	}

	&.uploaded {
		padding-bottom: 0;
		border-bottom: none;
		margin-bottom: 20px;

		.exe {
			& ~ div {
				display: flex;
				justify-content: space-between;
				flex: 1 0 auto;
			}
		}
	}
}

.exe {
	height: 35px;
	min-width: 40px;
	background: #f4f4f4;
	border: 1px solid #d9d9d9;
	display: flex;
	justify-content: center;
	align-items: center;
	font-size: rem(10);
	font-family: $taj;
	text-transform: uppercase;
	margin-left: 8px;
	margin-bottom: 0;
	padding: 0 10px;
}

.postioning-toast {
	position: fixed;
	top: 100px;
	right: 100px;
	z-index: 10000;

	.toast-style {
		color: #fff;

		.toast-body {
			padding: 15px 12px;
			font-size: rem(14);
			line-height: rem(22);
			margin-bottom: 20px;
			display: flex;
			align-items: center;
			border-radius: 5px;

			i {
				margin-left: 14px;
			}

			button {
				background-color: transparent;
				border-color: transparent;
				color: #fff;
				padding-right: 20px;

				&:focus {
					outline: none;
				}
			}
		}

		&.success_toast {
			color: #00b4a6;
			background-color: #00b4a633;

			.toast-body {
				border: 1px solid #00b4a6;
			}
		}

		&.error_toast {
			color: #ff595a;
			background-color: #ff595a33;

			.toast-body {
				border: 1px solid #ff595a;
			}
		}
	}
}

.no-noti {
	color: #000;
	text-align: center;
	width: 100%;
	margin-bottom: 8px;
}
.sidebar-policy {
	display: flex;
	flex-direction: column;
	place-items: flex-end;
	.logo {
		background-image: url('../../images/logo-black.svg');
	}
	.contact-ways {
		margin-top: 30px;
		p {
			direction: ltr;
			text-align: left;
			color: #000033;
			font-size: rem(10);
			line-height: rem(17);
			&:first-of-type {
				font-family: $DiodrumB;
				font-size: rem(11);
				margin-bottom: 20px;
			}
		}
	}
	.copyright {
		margin-top: 25px;
		font-size: rem(10);
		color: #707070;
	}
}
.privacy-policy_content {
	li {
		font-family: $taj;
		line-height: rem(24);
		color: #181b1f;
		list-style: none;
		position: relative;
		&:before {
			content: '';
			background: #0000000d;
			display: inline-block;
			width: 12px;
			height: 12px;
			margin-right: -14px;
			border-radius: 50%;
			position: absolute;
			right: -14px;
			top: 5px;
			// margin-left: 14px;
		}
	}
}

.container-post__left_side {
	.tab-content.more-info {
		div.block-content_side {
			padding: 10px 20px;
			& > h2 {
				font-family: $DiodrumB;
				font-size: rem(14);
				color: #000;
				margin-top: 15px;
				margin-bottom: 30px;
			}

			& > p {
				color: #707070;
				font-size: rem(12);
				line-height: rem(20);
			}
		}
	}
}
/* end of pages styling */
// media for small laptops & down
@media (max-width: 1280px) {
	.sign-window {
		h1 {
			margin-bottom: 15px;
			font-size: rem(30);
			line-height: rem(30);
			margin-top: 35px;
		}
		p {
			margin-bottom: 0;
		}
		.sign-window-form {
			margin-top: 20px;
		}
	}
}
@media (max-width: 1024px) and (min-width: 992px) {
	html {
		font-size: 12px;
	}

	body {
		&.fixed-sidebar {
			.sidebar {
				width: 245px;
			}
		}
	}
	.section-lg {
		&.center-img {
			background-size: 375px;
			background-position: -20px 260px;
		}
	}
	.blue-section {
		margin-top: -6px;
	}
	.section-md {
		.section-img {
			width: 448px;
		}
		&#path-id {
			margin-top: -2px;
			.hero-wrapper {
				&:nth-of-type(2) {
					.section-img {
						width: 445px;
					}
				}
				&:nth-of-type(3) {
					.section-img {
						width: 364px;
					}
				}
			}
		}
		&.common-section {
			.section-img {
				width: 356px;
			}
		}
	}
	.green-section {
		margin-top: -2px;
	}

	.line-through-section {
		.card-wrapper {
			&:first-of-type {
				padding: 0 170px;
			}

			&:nth-of-type(2) {
				margin: 42px 0px 40px -15px;
			}

			&:nth-of-type(3) {
				padding: 0 170px;
			}
		}
	}
	.workshop_partition {
		padding: 12px 10px;
		span + span {
			margin-right: 5px;
			margin-left: 5px;
		}
	}
}

@media (max-width: 991px) and (min-width: 768px) {
	.section-lg.center-img {
		background-size: 360px;
	}
	.section-md {
		.section-img {
			width: 308px;
		}
		.section-img + .section-text,
		.section-text + .section-img {
			margin-right: 30px;
		}
		&#path-id {
			margin-top: -1px;
			.hero-wrapper {
				&:nth-of-type(2) {
					.section-img {
						width: 308px;
					}
				}
				&:nth-of-type(3) {
					.section-img {
						width: 308px;
					}
					.section-text p:last-of-type {
						max-width: 299px;
					}
				}
			}
		}
		&.line-through-section {
			.line-throught-container {
				background-size: 710px;
			}
			.card-wrapper {
				&:first-of-type {
					padding: 0 45px;
				}
				&:nth-of-type(2) {
					margin: 20px -32px 20px -32px;
				}
				&:nth-of-type(3) {
					padding: 0 45px;
				}
			}
		}
		&.common-section {
			.section-text {
				flex: 0 0 352px;
			}
		}
		&.blue-section {
			margin-top: -2px;
			.section-img {
				width: 308px;
			}
		}
	}
	body.fixed-sidebar {
		main {
			height: 100%;
		}
		.sidebar {
			width: 200px;
			top: 165px;
		}
	}
	.main-header {
		.logo,
		.nav {
			margin-bottom: 20px;
		}
	}
	main {
		.container-post-wrapper {
			margin-top: 8px;
			.container-post__center {
				flex: 0 0 80%;
				max-width: 80%;
			}
			.container-post__right_side {
				flex: 0 0 20%;
				max-width: 20%;
			}
			.container-post__left_side {
				display: none;
			}
		}
	}
	#nav_wrap.logged ~ main {
		padding-top: 136px;
	}
	.card-testmonilas {
		width: 100%;
		.front {
			background-size: cover;
		}
	}
	.content-section_header.tabs {
		.list-group {
			flex-wrap: wrap;
		}
	}
}
