.form-group {
	margin-bottom: 15px;

	.form-control {
		padding: 10px 20px;
		min-height: 40px;
		background-color: transparent;
		font-family: $cairo;
		font-weight: $med;
		border: 1px solid #ffffff;
		color: #fff;
		&.text-black-50 {
			&::placeholder {
