		border-radius: $border-radius-sm;
		padding-left: 115px;
		width: 100%;

		&::placeholder {
			text-align: right;
		}
	}

	.btn {
		font-size: 14px;
		line-height: 1rem;
		width: 95px;
		position: absolute;
		bottom: 0;
		left: 0;
		height: 40px;
	}

	&.has-error {
