		}
	}
}

.form-group_search {
	margin-right: 60px;
	margin-left: 30px;
	margin-bottom: 0;
	height: 35px;
	position: relative;

	.form-control {
		border-color: #000;
		min-width: 350px;
		min-height: auto;
		height: 35px;
		padding-right: 43px;
		padding-left: 30px;
		color: #000;

		&::placeholder {
			color: #707070;
			font-size: rem(12);
		}
	}

	a.filter-search {
		position: absolute;
		left: 0;
		padding: 10px;
		transition: padding 0.2s ease-in-out;
		top: -2px;

		.icon-tringle-down {
			left: 10px;
			font-size: rem(5);
		}
