	&.show {
		a.filter-search {
			padding-left: 15px;
		}

		.form-control {
			box-shadow: $boxShadow;
			border-color: transparent;
		}
	}
}

// custom checkbox
.custom-control.custom-checkbox {
	width: auto;
	display: inline-block;
	margin-bottom: 22px;
	padding-left: 0;
	margin-left: 14px;

	.custom-control-label {
		padding-right: 26px;
		font-size: rem(12);
		line-height: rem(24);
		font-family: $cairo;
		font-weight: $bold;
		width: 100%;

		&:before {
			right: 0;
			left: auto;
			border-radius: 0;
			border-width: 2px;
			border-color: rgba(#000000, 0.38);
			z-index: 1100;
		}

		&:after {
			right: 0;
			left: auto;
		}
	}

	.custom-control-input {
		&:checked {
			~ .custom-control-label::before {
				// check icon
				content: '\e90b';
				font-family: 'icomoon' !important;
				speak: none;
				font-style: normal;
				font-weight: normal;
				font-variant: normal;
				text-transform: none;
				line-height: 1;
				-webkit-font-smoothing: antialiased;
				-moz-osx-font-smoothing: grayscale;
				// end check icon
				background-color: transparent;
				color: $secondary;
				font-size: rem(6);
				text-align: center;
				line-height: rem(10);
				border-color: $secondary;
				outline: 0;
				box-shadow: none;
			}
		}

		&:focus {
			.custom-control-label::before {
				outline: 0;
				box-shadow: none;
			}

			&:not(:checked) {
				~ .custom-control-label::before {
					border-color: $black;
					outline: 0;
					box-shadow: none;
				}
			}
		}
	}
}

