// styles form delete model 
.reason-model {
    .modal-dialog {
        max-width: 650px;
        direction: rtl;

        .modal-content {
            border: none;
            border-radius: 0;
        }

        .modal-header {
            background-color: $warning;
            color: $white;
            border-top-left-radius: 0;
            border-top-right-radius: 0;

            h5 {
                font-size: rem(12);
                line-height: rem(22);
                display: flex;
                align-items: center;

                span {
                    width: 32px;
                    height: 32px;
                    border-radius: 50%;
                    background-color: #0000001C;
                    display: inline-flex;
                    justify-content: center;
                    align-items: center;
                    font-size: rem(18);
                    margin-left: 16px;

                    .icon-flag {
                        color: #00000099;
                        font-size: rem(16);

                    }
                }
            }


        }

        form div:not(.form-group) {
            margin-top: 45px;
            display: flex;
            justify-content: flex-end;

            .btn {
                font-size: rem(14);
                line-height: rem(14);
                padding: 10px 20px;

                &+.btn {
                    margin-right: 20px;
                }
            }

        }

    }
}

.modal.new-content {
    .modal-dialog {
        max-width: 650px;

        .modal-content {
            border-radius: 8px;

            .modal-header {
                padding: 20px 24px 8px;
                background-color: #0000001C;

                h4 {
                    font-size: rem(18);
                    font-family: $DiodrumB;
                    margin-bottom: 0;
                    line-height: rem(25);
                }
            }

            .modal-body {
                padding: 15px 24px 22px;

                .new-content_info {
                    padding: 22px;
                    background-color: #F5F6F8;
                    display: flex;
                    border-radius: 4px;

                    span {
                        margin-left: 16px;
                        font-size: rem(13);
                        line-height: rem(25);

                        i {
                            color: #9E9E9E;
                        }

                    }

                    p {
                        font-size: rem(12);
                        line-height: rem(25);
                        margin-bottom: 0;
                    }
                }
            }

            .model-footer {
                padding: 13px 37px 22px 26px;
                display: flex;
                align-items: center;
                justify-content: space-between;

                p {
                    margin-bottom: 0;
                }

                .open-sources {
                    display: flex;
                    align-items: center;
                    color: #00000061;

                    i {
                        font-size: rem(22);
                        margin-left: 12px;
                    }

                    p {
                        font-size: rem(14);
                        font-family: $DiodrumL;
                        font-weight: $bold;
                    }
                }

                .btn {
                    padding: 12px 26px;

                    &+.btn {
                        margin-right: 30px;
                    }
                }
            }

        }


    }
}

.modal.new-content {
    &.new-group {
        .modal-title {
            font-size: rem(18);
            font-family: $DiodrumB;
        }

        .modal-dialog .modal-content {
            .modal-body {
                .new-content_info {
                    position: relative;
                    padding: 0 !important;
                    margin-bottom: 20px;

                    &>* {
                        &:first-of-type {
                            padding: 27px;
                            flex: 0 0 80%;
                            max-width: 80%;
                            display: flex;
                            border-top-right-radius: 4px;
                            border-bottom-right-radius: 4px;


                            span {
                                i {
                                    color: #FFFFFF99;
                                }
                            }
                        }

                        &:last-of-type {
                            clip-path: circle(100.0% at 0 51%);
                            flex: 0 0 26%;
                            max-width: 26%;
                            display: flex;
                            width: 100%;
                            position: absolute;
                            left: 0;
                            top: 0;
                            bottom: 0;
                            border-top-left-radius: 4px;
                            border-bottom-left-radius: 4px;

                            .avatar {
                                display: none;
                                width: 80px;
                                height: 80px;
                            }

                            div {
                                &:first-of-type {
                                    display: flex;
                                    width: 100%;
                                    justify-content: center;
                                    align-items: center;
                                    flex-direction: column;
                                    color: #FFF;
                                    background-color: #aaa;
                                    font-size: rem(12);
                                    margin-right: 20px;
                                }

                                i {
                                    font-size: 24px;
                                    margin-bottom: 5px;
                                }
                            }

                            input {
                                width: 0 !important;
                                height: 0 !important;
                                opacity: 0;
                                display: none !important;
                            }

                            .uppy-FileInput-container {
                                position: absolute;
                                width: 100%;
                                height: 100%;
                                margin-bottom: 0;

                                .uppy-FileInput-btn {
                                    width: 100%;
                                    height: 100%;
                                    opacity: 0;
                                }
                            }

                            &.file-choosen {
                                background-color: #FFC055;
                                justify-content: center;
                                align-items: center;

                                div {
                                    &:first-of-type {
                                        display: none;
                                    }
                                }

                                .avatar {
                                    display: block;
                                }
                            }
                        }
                    }

                    p {
                        color: #121212;
                        max-width: 80%;
                    }
                }
            }

            .model-footer {
                padding: 0;
                padding-top: 15px;
                border-top: 1px solid #E4E4E4;
                margin-top: 30px;
            }
        }
    }

}

.new-workshop {
    &.modal .modal-dialog .modal-content {
        .modal-title {
            font-size: rem(18);
            font-family: $DiodrumB;
        }

        .model-footer {
            padding: 0;
            padding-top: 15px;
            border-top: 1px solid #E4E4E4;
            margin-top: 30px;

            &>* {
                &:first-of-type {
                    display: flex;
                    align-items: center;

                    i {
                        margin-left: 9px;
                        font-size: rem(20);
                    }

                    p {
                        color: #383838;
                        font-family: $DiodrumL;
                        font-weight: 700;
                    }
                }
            }
        }
    }

}