.tag {
    border: 1px solid #6A6A6A;
    border-radius: $border-radius-sm;
    color: #707070;
    font-size: rem(12);
    line-height: rem(14);

    &.tag-sm {
        padding: 3px 10px 4px;
        line-height: rem(11);
        position: relative;
        z-index: 2;
        margin-bottom: 10px;
    }

    &.tag-related {
        border-radius: 11px;
    }
}

.filter-tag {
    width: 66px;
    height: 23px;
    padding: 5px;
    background-color: #EEEEEE;
    border: none;
    color: $black;
    font-family: $cairo;
    font-weight: $bold;

    i {
        font-size: rem(8);
        margin-right: 10px;
        color: #707070;
    }
}