// layout div
.info-preview {
    display: flex;
    justify-content: flex-start;
    align-items: center;

    &.info-preview__lg {
        flex-direction: column;
        justify-content: center;
        border-bottom: 1px solid #cccccc;

        .avatar {
            margin-bottom: 35px;
        }

        .info {
            max-width: 100%;
            margin-right: 0;
            text-align: center;
            margin-bottom: 28px;

            .info-title {
                font-size: rem(20);
                line-height: rem(26);
                margin-bottom: 22px;
            }
        }
    }
}

.info {
    flex: 0 1 auto;
    max-width: 75%;

    .info-title {
        font-size: rem(16);
        line-height: rem(20);
    }

    .info-subtitle {
        font-size: rem(12);
        margin-bottom: 0;
        color: rgba(#000033, 0.5);
        margin-bottom: 0;
    }

    &.info-text-sm {
        .info-title {
            font-size: rem(11);
            line-height: rem(16);
            margin-bottom: 0;
        }

        .info-subtitle {
            font-size: rem(8);

        }
    }
}

.avatar {
    &+.info {
        margin-right: 15px;
    }

    &+.info-sm {

        .info-title,
        .info-subtitle {
            white-space: nowrap;
            text-overflow: ellipsis;
            overflow: hidden;
        }
    }
}

// info preview into card testmonilas
.card-testmonilas {
    .info {
        margin-right: 25px;
        font-size: rem(14);

        .info-title {
            font-family: $DiodrumB;
        }

        .info-subtitle {
            font-size: rem(11);
        }
    }
}

.workshop-collebrators {
    display: flex;
    justify-content: space-between;
    align-items: center;

    &+.workshop-collebrators {
        margin-top: 20px;
    }
}


@media (max-width: 991px) and (min-width: 768px) {
    .card-testmonilas {
        .info-preview {
            max-width: 100%;
            .avatar.avatar-lg  {
                width: 40px;
                height: 40px;
                &+ .info {
                    max-width: 75%;
                    margin-right: 10px;
                }
            }
        }
    }

}