footer {
	padding-top: 58px;
	padding-bottom: 56px;
	color: #fff;
	display: none;

	.footer-wrapper {
		font-family: $DiodrumB;
	}

	.nav {
		padding-right: 0;
		margin-top: 38px;

		.nav-item:first-child {
			a {
				padding-right: 0;
			}
		}

		a {
			color: #cccccc;

			&:hover {
				color: #cccccc;
			}
		}
	}
	.logo {
		display: inline-block;
		&:nth-of-type(2) {
			width: 125px;
		}
		&:nth-of-type(3) {
			width: 68px;
		}
		& + .logo {
			margin-right: 30px;
		}
	}
}
.contact-side {
	text-align: left;

	& > p:first-of-type {
		margin-bottom: 19px;
	}

	.contact-ways {
		p {
			color: #cccccc;
			line-height: rem(28);
			margin-bottom: 0;
			font-family: $cairo;

			&:first-of-type {
				direction: ltr;
			}
		}
	}
}

.soical-icons {
	display: flex;
	flex-direction: row-reverse;
	margin-top: 20px;

	li {
		a {
			color: #75787b;
			font-size: rem(18);
		}

		& + li {
			margin-left: 15px;
		}
	}
}

.copyright {
	text-align: left;
	font-size: rem(12);
	line-height: rem(13);
	font-family: $taj;
}

main.bg-mo-gray {
	& ~ footer {
		background-color: #fff !important;
		color: $blue;

		.footer-wrapper {
			.nav a {
				color: #000;
				font-family: $DiodrumM;
			}

			.form-group .form-control {
				color: #707070;

				&::placeholder {
					color: #707070;
				}
			}
		}

		.contact-side {
			& > p {
				font-family: $DiodrumB;
			}

			.contact-ways {
				p {
					color: $blue;
				}
			}
		}

		.copyright {
			color: #707070;
		}

		// .logo {
		// 	background-image: url('../../images/logo-black.svg');
		// }
	}
}

.sign-header {
	& ~ footer {
		display: none;
	}
}
