// avatar img sizes => 35 ,40 , 45 , 52, 130
// avatar component
.avatar {
	width: 40px;
	height: 40px;
	border-radius: 50%;
	display: inline-block;
	flex-shrink: 0;
	background-color: $blue;
	background-image: url('../../images/logo-sm.svg');
	background-size: 12px;
	background-position: center;
	background-repeat: no-repeat;

	&[data-avatar] {
		background-size: cover !important;
	}

	&.avatar-xxs {
		width: 22px;
		height: 22px;
		background-size: 8px;
	}

	&.avatar-xs {
		width: 30px;
		height: 30px;
	}

	&.avatar-sm {
		width: 35px;
		height: 35px;
	}

	&.avatar-md {
		width: 45px;
		height: 45px;
	}

	&.avatar-lg {
		width: 52px;
		height: 52px;

		& + .info {
			max-width: 69%;
		}
	}

	&.avatar-xl {
		width: 130px;
		height: 130px;
		background-size: 44px;
		&.placeholder {
			background-size: 44px !important;
		}
	}

	&.changeable {
		display: flex;
		justify-content: center;
		align-items: flex-end;
		overflow: hidden;
		position: relative;

		.change-photo {
			background-color: $secondary;
			width: 100%;
			height: 35px;
			display: flex;
			align-items: center;
			justify-content: center;
			color: #fff;

			p {
				margin-bottom: 0;
				font-family: $DiodrumL;

				i {
					margin-left: 5px;
					font-size: rem(10);
				}
			}
		}

		.uppy-FileInput-container {
			opacity: 0;
			position: absolute;
		}
	}
}
