//Variables
$baseFontSize: 14px;

/* fonts */
//DiodrumArabic font
$DiodrumEL: 'DiodrumArabic-Extralight',
'sans-serif';
$DiodrumL: 'DiodrumArabic-Light',
'sans-serif';
$DiodrumM: 'DiodrumArabic-Medium',
'sans-serif';
$DiodrumB: 'DiodrumArabic-Bold',
'sans-serif';

$cairo: 'Cairo',
sans-serif;
$taj: 'Tajawal',
sans-serif;

$notoKufi: 'NotoKufiArabic',
sans-serif;
$notoKufiB: 'NotoKufiArabic-Bold',
sans-serif;

/* end of fonts */

$light: 300;
$reg: 400;
$med: 500;
$semi: 600;
$bold: 700;
$i: italic;

$boxShadow: 0px 3px 6px #00000029;

////Mixins
//Bootstrap
@import '../node_modules/bootstrap/scss/mixins/_breakpoints.scss';

// Typo, mixin to easly include fontface rule
// @params: $font-family: font name, $file-path: path to the font files on server
@mixin font-face($font-family,
    $file-path,
    $font-weight: normal,
    $font-style: normal) {
    @font-face {
        font-family: $font-family;
        src: url('#{$file-path}.eot');
        src: url('#{$file-path}.eot?#iefix') format('embedded-opentype'),
        url('#{$file-path}.woff2') format('woff2'),
        url('#{$file-path}.woff') format('woff'),
        url('#{$file-path}.ttf') format('truetype'),
        url('#{$file-path}.svg##{$font-family}') format('svg');
        font-weight: $font-weight;
        font-style: $font-style;
    }
}

////Functions
//px to rem function, function to auto transform px to rems
// @params: $pixels: px unit
@function rem($pixels, $context: $baseFontSize) {
    @if unitless($pixels) {
        $pixels: $pixels * 1px;
    }

    @if unitless($context) {
        $context: $context * 1px;
    }

    @return $pixels / $context * 1rem;
}

@mixin not-last-child($selector) {
    & #{$selector}:not(:last-child) {
        @content;
    }
}