.card {
	color: $black;
	height: 100%;

	&:not(.card_sm) {
		box-shadow: $boxShadow;
	}

	&.card_sm {
		border: 1px solid #000033;
		border-radius: 19px;
		width: 208px;
		padding: 14px 23px;
	}

	.card-img-top {
		background-color: #eee;
	}

	.card-header {
		font-size: rem(10);
		line-height: rem(14);
		padding: 12px $card-spacer-x;

		P {
			margin-bottom: 0;
		}
	}

	.card-body {
		padding: 0 $card-spacer-x;

		.card-title {
			font-size: rem(18);
			line-height: rem(22);
			margin-bottom: 18px;
		}

		.card-text {
			font-size: rem(12);
			line-height: rem(20);
			margin-bottom: 23px;
		}
	}

	.card-footer {
		padding: 0 $card-spacer-x;
		padding-bottom: 17px;
		z-index: 2;

		.info-preview {
			margin-top: 5px;

			.info {
				.info-title {
					font-size: rem(16);
					line-height: rem(20);
					margin-bottom: 0;

					// max-width: 150px;
				}

				.info-subtitle {
					// max-width: 150px;
					font-size: rem(12);
				}
			}
		}

		.card-details {
			display: flex;
			justify-content: space-between;
			flex-wrap: wrap;
			justify-content: space-between;
			align-items: center;
			margin-top: 20px;

			& > * {
				flex: 0 0 50%;
				font-size: rem(12);
				line-height: rem(20);
				margin-bottom: 18px;
				display: flex;
				align-items: center;
			}

			i {
				font-size: rem(11);
				color: #707070;
				margin-left: 5px;

				&.icon-icon-group,
				&.icon-icon-clock {
					font-size: rem(16);
				}
			}
		}
	}

	.card-label {
		justify-content: space-between;
		align-items: flex-start;
	}

	.special-content {
		display: flex;
	}

	.tag {
		&:not(:last-of-type) {
			margin-left: 12px;
		}
	}

	&.preview-card {
		padding: 30px 0;

		.card-body {
			padding: 0 42px;

			.card-text {
				margin-top: 18px;
				font-size: rem(13);
				line-height: rem(25);
				color: #000033;
			}
		}
	}

	&.mo-card-group {
		padding: 0 5px;

		.card-header {
			border-bottom: 1px solid #eeeeee;
		}

		.card-body {
			padding-bottom: 20px;
			border-bottom: 1px solid #eeeeee;

			.info-preview {
				margin-top: 15px;
			}
		}

		.card-footer {
			padding: 15px;
			text-align: center;

			h6 {
				font-size: rem(12);
				line-height: rem(14);
				color: #707070;
			}
		}
	}

	&.card-draft {
		flex-direction: row;
		padding: 43px 20px;

		.draft-label {
			position: absolute;
			top: 13px;
			color: #0041cb;
			display: flex;
			align-items: center;

			span {
				font-size: rem(10);
				line-height: rem(14);
			}

			i {
				font-size: rem(16);
				margin-left: 10px;
			}
		}

		.card-body {
			flex: 0 0 auto;
			max-width: 100%;
			display: flex;
			padding: 0;

			.card-title {
				flex: 0 0 30%;
				margin-left: 20px;
				margin-bottom: 0;
				min-width: 115px;

				p {
					color: #00000061;
					font-size: rem(10);
					line-height: rem(14);
					margin-top: 15px;

					span {
						color: $black;
						margin-right: 5px;
					}
				}
			}

			.card-text {
				flex: 0 0 60%;
				color: $blue;
				margin-bottom: 0;
			}
		}
	}

	&.card-folder {
		padding: 25px 15px;

		.card-body {
			padding: 0;
			display: flex;
			flex-direction: column;

			// &>* {
			//     &:first-of-type {
			//         flex: 0 0 15%;
			//         font-size: rem(13);
			//         color: #707070;
			//     }
			// }

			.card-title {
				margin-bottom: 10px;
				display: flex;
				align-items: center;

				i {
					font-size: rem(13);
					line-height: rem(21);
					color: #707070;
					margin-left: 15px;
				}
			}

			.card-text {
				margin-bottom: 0;
				margin-right: 31px;
			}
		}
	}

	&.card-member {
		.card-header {
			border-bottom: 1px solid #eeeeee;
			margin: 12px 20px;
			padding: 0;
			padding-bottom: 15px;
		}

		.card-text {
			line-height: rem(25);
			margin-bottom: 0;
		}

		.card-footer {
			padding: 0;
			margin: 20px;
			padding-top: 15px;
			text-align: center;
			font-size: rem(12);
			border-top: 1px solid #eeeeee;

			a {
				color: #707070;
			}
		}
	}

	&.card-titles {
		height: auto;

		.card-body {
			padding-top: 14px;
			padding-bottom: 50px;
		}
	}

	&.card-author_history {
		height: auto;

		.card-header {
			.info {
				max-width: 100%;

				div {
					display: flex;
					align-items: center;
					margin-bottom: 12px;

					span {
						display: flex;
						width: 35px;
						height: 35px;
						border-radius: 50%;
						justify-content: center;
						align-items: center;
						background-color: #ffc055;
						margin-left: 15px;
						font-size: rem(17);
					}
				}

				.info-title {
					margin-bottom: 3px;
				}

				.info-subtitle {
					max-width: 90%;
					margin: auto;
				}
			}
		}

		.card-body {
			padding-top: 20px;
			padding-bottom: 30px;
			border-top: 1px solid #eeeeee;

			.info-preview {
				.avatar {
					position: relative;

					.icon-pen-cirlce {
						position: absolute;
						top: 50%;
						transform: translateY(-50%);
						left: -5px;
						color: #ffc055;
						font-size: rem(16);

						&:before {
							color: #000;
						}
					}
				}

				& + .info-preview {
					margin-top: 16px;
				}
			}
		}

		.card-footer {
			padding: 15px;
			text-align: center;
			border-top: 1px solid #eee;

			h6 {
				font-size: rem(12);
				line-height: rem(14);
				color: #707070;
			}
		}
	}

	&.card-comment {
		padding: 0;
		border-radius: 10px;
		box-shadow: none;
		margin-bottom: 21px;

		.comment-details_wrapper {
			padding: 30px 30px 0;
			display: flex;
			justify-content: space-between;
			align-items: center;
		}

		.info-preview {
			.avatar {
				box-shadow: 0px 4px 10px #00000029;
			}

			.info-title {
				color: $blue;
			}

			& > * {
				display: flex;
				align-items: center;
			}
		}

		.comment-time {
			color: #707070;
			display: flex;

			i {
				margin-left: 8px;
				font-size: rem(13);
			}

			span {
				font-size: rem(11);
			}
		}

		.card-body {
			padding: 20px 75px 30px 0;
			font-family: $taj;

			p {
				margin-bottom: 0;
				line-height: rem(24);
				margin-left: 23px;
			}
		}

		.form-group_comment {
			padding-right: 30px;
			border: none;
			border-top: 1px solid #d5d5d5;
			border-radius: 0px 0px 10px 10px;

			.form-control {
				border-radius: 10px;
				resize: none;
			}
		}

		.card-comment_reply {
			background-color: #f4f4f4;
			border-radius: 0px 0px 5px 5px;
			border: 1px solid #d5d5d5;
			border-bottom: none;
		}

		.card-comment {
			margin-bottom: 0;
			border-radius: 0;
		}
	}

	&.related-posts_card {
		.card-header {
			color: #707070;
			padding: 20px 25px 30px 25px;
		}

		.card-body {
			padding: 0 25px;
		}

		.card-footer {
			padding: 0 25px;
			padding-bottom: 25px;
		}
	}
}

.special-content {
	display: flex;
	background-color: #fff;
	border: 2px solid $blue;
	border-radius: 3px;
	padding: 5px 7px;
	right: 0;
	justify-content: flex-end;

	span {
		font-size: rem(10);
		line-height: rem(14);
		margin-right: 5px;
	}

	i {
		font-size: rem(16);
	}
}

.card-controls,
.special-content {
	position: absolute;
	top: 0;
	z-index: 2;
}

.card-controls {
	margin-top: 15px;
	left: 20px;
	direction: ltr;

	a {
		color: #707070;
		display: inline-block;

		& + a {
			margin-left: 15px;
		}

		&:not(.bookmark) {
			font-size: rem(16);
		}

		&.bookmark {
			color: $white;
			font-size: rem(21);
		}
	}

	.bookmarked & {
		.bookmark {
			color: $primary;
		}
	}
}

.left-side_info #postsSidebar {
	.card-controls {
		display: none;
	}
}

.card-testmonilas {
	width: 312px;

	// height: 471px;
	min-height: 471px;
	perspective: 500px;

	.content {
		// height: 100%;
		transition: transform 1s;
		transform-style: preserve-3d;
		min-height: 471px;
	}

	&:hover {
		.content {
			transform: rotateY(180deg);
			transition: transform 0.5s;
		}
	}

	.front,
	.back {
		position: absolute;
		width: 100%;
		border-radius: 10px;
		backface-visibility: hidden;
		box-shadow: 0px 10px 36px #00000029;
	}

	.front {
		background-size: cover;
		background-repeat: no-repeat;
		background-position: center;
		height: 100%;
	}

	.back {
		background-color: #ffc055;
		color: $black;
		padding: 35px 22.5px;
		transform: rotateY(180deg);
		display: flex;
		flex-wrap: wrap;
		min-height: 100%;

		& > h4 {
			font-size: rem(24);
			line-height: rem(40);
			font-family: $DiodrumB;
			width: 258px;
			margin-bottom: 45px;
		}
	}
}

.workshop-comment-wrapper {
	margin-bottom: 20px;
	margin-top: 10px;

	.comment-view {
		margin-top: 24px;
		border: 1px solid #9164cc;
		font-size: rem(14);
		line-height: rem(24);
		font-family: $DiodrumL;
		font-weight: $bold;
		border-radius: 5px;
		background-color: #fcf0e380;

		p {
			padding: 13px 10px 26px 13px;
			background: #fcf0e380;
			margin-bottom: 0;
		}
	}
}

.workshop-reply-wrapper {
	padding-right: 20px;
	margin-bottom: 40px;
	.info {
		.info-title {
			font-size: rem(12);
		}
		.info-subtitle {
			font-size: rem(10);
		}
	}

	.comment-view {
		border-color: #d5d5d5;
		background-color: #f4f4f4;
	}
}

@media (max-width: 991px) and (min-width: 768px) {
	.card-testmonilas {
		.back > h4 {
			font-size: rem(18);
			line-height: rem(30);
		}
	}

	.card {
		&.card-titles {
			.card-header {
				.info-preview {
					flex-wrap: wrap;
					justify-content: center;

					.avatar {
						margin-bottom: 10px;
					}
				}
			}
		}

		&.card-author_history {
			.avatar + .info {
				margin-right: 7px;
			}
		}

		&.preview-card {
			.card-body {
				padding: 0 15px;
			}
		}
	}
}
