.btn {
	color: #000;
	font-size: rem(12);
	line-height: rem(14);

	&:hover,
	&:focus,
	&:active,
	&:not(:disabled):not(.disabled):active {
		color: #000;
	}

	span + i,
	i + span {
		margin-right: 25px;
		vertical-align: middle;
		display: inline;
	}

	i {
		font-size: rem(16);

		&.icon-plus {
			font-size: rem(8);
		}
	}
}

.btn-lg {
	font-size: rem(14);
	line-height: rem(17);
}

.btn-search {
	padding: 10px;
	margin: 0;
	position: absolute;
	right: 0;
	top: 0;

	i {
		font-size: rem(12);
	}
}

.btn-sm {
	display: block;
	font-size: rem(12);
	font-weight: $bold;
	padding: 8px 20px;
}

.btn-secondary,
.btn-pink {
	color: #fff;

	&:hover {
		color: #fff;
	}
}

.btn-outline-secondary {
	border-width: 2px;
	padding: 5px 15px;
	line-height: rem(17);
}

.btn-outline-dark {
	color: #707070;
	border-color: #707070;
}

.btn-outline-blue {
	color: $blue;
	border-color: $blue;
}

.notifications-bell {
	color: #aaaaaa;
	padding-left: 15px;
	padding-right: 15px;
	font-size: rem(16);
	position: relative;
	margin-left: 15px;
	margin-right: 15px;

	i.icon-Notifi-Badge {
		color: #ffc055;
		font-size: rem(8);
		width: 10px;
		height: 10px;
		background: #fff;
		position: absolute;
		top: 5px;
		left: 30%;
		border-bottom-right-radius: 7px;
	}
}

.content-section_btn {
	box-shadow: $boxShadow;
	font-size: rem(14);
	line-height: rem(17);
	padding: 7px 23px;
}

.view-tab {
	font-family: $DiodrumL;
	font-weight: $bold;
	font-size: rem(14);
	line-height: rem(17);
	padding: 6px 20px;
	position: relative;
	display: flex;
	align-items: center;

	&:not(:first-of-type) {
		&:not(:only-child) {
			margin-left: 10px;

			&::after {
				content: '';
				position: absolute;
				border-left: 1px solid #cccccc;
				height: 100%;
				top: 0;
				right: 0px;
			}
		}
	}

	&:not(:only-child) {
		&::after {
			display: none;
		}
	}

	&:not(:first-of-type) {
		margin-right: 10px;
	}

	i {
		margin-left: 12px;

		&.icon-posts {
			font-size: rem(10);
		}

		&.icon-bookmark {
			font-size: rem(12);

			&:before {
				color: transparent;
			}
		}

		&.icon-arrow-right {
			font-size: rem(12);
		}

		&.icon-icon-users,
		&.icon-save {
			font-size: rem(11);
		}

		&.icon-cog {
			font-size: rem(16);
		}
	}

	&.active {
		box-shadow: $boxShadow;
		background-color: $white;
	}

	&.btn-primary {
		box-shadow: $boxShadow;
	}

	&#backToView {
		display: none;
	}
}

.article-title {
	color: #707070;
	font-size: rem(12);
	line-height: rem(20);
	display: block;
	max-width: 100%;
	text-align: right;
	word-break: break-word;
	padding-bottom: 15px;

	&:hover {
		text-decoration: underline;
	}

	&:not(:last-of-type) {
		border-bottom: 1px solid #f4f4f4;
	}

	&.active {
		color: darken($link-color, 15%);
	}

	& + .article-title {
		padding-top: 15px;
	}
}

.report-btn {
	color: #00000099;
	display: flex;
	align-items: center;

	i + span {
		margin-right: 13px;
	}
}

.post-tab_btn {
	flex: 1 0 auto;
	color: #000033;
	font-size: rem(14);
	background-color: #0000001c;

	&.active {
		background-color: #ffc055;
	}

	& + .post-tab_btn {
		margin-right: 7px;
	}
}

.add-workshop_partition {
	border: 1px dashed #000;
	font-family: $DiodrumB;
	padding: 14px 20px 14px 12px;
	margin: 10px auto;

	i {
		&.icon-plus {
			font-size: rem(8);
		}

		&.icon-tringle-down {
			font-size: rem(5);
		}
	}

	span + i,
	i + span {
		margin-right: 7px;
	}
}

.btn-days_wrapper {
	overflow: auto;
	padding: 13px 0;

	a {
		color: #00000061;
		font-size: rem(12);
		font-family: $DiodrumB;
		padding: 12px 20px;
		border-radius: 0;
		border-bottom: 1px solid #707070;
		flex: 0 0 auto;

		&[href='#'] {
			border-bottom: 1px dashed #707070;
		}

		&.active {
			color: #000;
		}

		span + i,
		i + span {
			margin-right: 7px;
		}

		i.icon-minus {
			width: 16px;
			height: 16px;
			border-radius: 50%;
			background-color: #fff;
			font-size: rem(2);
			display: inline-flex;
			justify-content: center;
			align-items: center;
			vertical-align: middle;
			margin-left: 10px;
		}

		& + a {
			margin-right: 22px;
		}
	}
}

#attachesSidebar,
#collaboratorSidebar {
	.btn {
		text-align: right;
		font-size: rem(14);

		&.btn-close {
			background-color: #f4f4f4;
		}

		span {
			font-size: rem(12);
		}

		i {
			font-size: rem(14);
		}

		i + span {
			margin-right: 10px;
		}

		& + .btn {
			margin-right: 20px;
		}
	}
}

@media (max-width: 991px) and (min-width: 768px) {
	.view-tab {
		padding: 6px 13px;
	}
}
