.dropdown-menu {
    width: 350px;
    text-align: right;
    border-top-left-radius: 0;
    border-top-right-radius: 0;
    margin: -3px 0 0;
    border: none;
    border-top: 1px solid #EEEEEE;
    padding: 21px 12.5px;
    box-shadow: $boxShadow;

    .btn-sm {
        margin-right: auto;
        margin-top: 50px;
        font-family: $cairo;
        font-size: rem(10);

    }

    .dropdown-notifitctions {
        max-height: 340px;
        overflow: scroll;
    }
}

.dropdown-sm {
    width: 300px;
    padding-left: 0;
    padding-right: 0;
    padding-bottom: 0;
    padding-top: 15px;
    box-shadow: 0px 2px 10px #00000027;
    top: 15px !important;
    font-family: $notoKufi;
    border-radius: 0;


    .dropdown-item {
        background-color: #FAFAFA;
        padding: 12px 30px;
        border-bottom: 1px solid #F1F1F3;

        .info {
            .info-title {
                font-size: rem(10);
                line-height: rem(19);
                color: #4D4F5C;
            }

            .info-subtitle {
                font-size: rem(11);
                line-height: rem(13);

            }
        }

        &.seen {
            background-color: #FFF;

        }
    }


    // &:after {
    //     content: '';
    //     position: absolute;
    //     display: block;
    //     border-color: transparent;
    //     border-style: solid;
    //     top: -8px;
    //     border-width: 0 .5rem .5rem .5rem;
    //     border-bottom-color: #fff;
    //     left: 16px;
    // }
}

.arrow-menu-top {
    width: 10px;
    height: 10px;
    position: absolute;
    background-color: #fff;
    left: 35px;
    top: 44px;
    transform: rotate(45deg);
    z-index: 1100;
    display: none;
}

.dropdown {
    &.show {
        .arrow-menu-top {
            display: block;
        }
    }

    .avatar {
        &~.arrow-menu-top {
            left: 15px;
            top: 47.5px;
        }
    }

    &+.dropdown {
        .dropdown-menu {
            padding-top: 0;
            width: 200px;

            .dropdown-item {
                font-size: rem(12);
                background-color: #fff;
            }
        }
    }
}

.dropdown-header {
    font-size: rem(17);
    line-height: rem(22);
    color: #4D4F5C;
    padding-top: 0;
    padding-bottom: 15px;
    text-align: center;

}

.dropdown-footer {
    padding: 20px;
    color: #FF9933;
    text-align: center;
    font-size: rem(12);
    line-height: rem(18);
    display: block;

    &:hover {
        color: #FF9933;
    }
}

.dropdown-workshop_partition {
    width: 135px;

    &.dropdown-menu {
        padding: 0;

        .dropdown-item {
            padding: 20px 38px;
            font-family: $notoKufiB;
            font-size: rem(10);

        }
    }
}